.toggle-check-input {
  width: 1px;
  height: 1px;
  position: absolute;
}

.toggle-check-text {
  display: inline-block;
  position: relative;
  text-transform: uppercase;
  background: #CCC;
  padding: 0.25em 0.5em 0.25em 2em;
  border-radius: 1em;
  min-width: 2em;
  color: #FFF;
  cursor: pointer;
  height: 30px;
  width: 50px;
  transition: background-color 0.15s;
}

.toggle-check-text:after {
  content: ' ';
  display: block;
  background: #FFF;
  width: 1.8em;
  height: 1.8em;
  border-radius: 1em;
  position: absolute;
  left: 0.08em;
  top: 0.05em;
  transition: left 0.15s, margin-left 0.15s;
}


.toggle-check-input:checked~.toggle-check-text {
  background: #34C759;
  padding-left: 0.5em;
  padding-right: 2em;
}

.toggle-check-input:checked~.toggle-check-textt:before {
  content: 'Yes';
  color: #34C759;
}

.toggle-check-textt:before {
  position: absolute;
  right: -32px;
  content: 'No';
  top: 2px;
  color: red;
}

.toggle-check-input:checked~.toggle-check-text:after {
  left: 86%;
  margin-left: -1.4em;
}

